import axios from 'axios'


export const loadmuvies = (muvi) =>{
  return { type: 'SET_VISIBBL', payload: muvi }
}



export const fetchData = (fetch)=> async (dispatch)=>{
   try {
   
    const data = await axios.post(fetch);
    dispatch(loadmuvies(data.data))
   

   } catch (error) {
     console.log(error);
     
   }

}
